.titleWidget {
    width: 25rem;
    height: 10rem;
    background: rgba(46,46,46,255);
    border-radius: 2rem;
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    top: 2.2rem;
}
.widgetContent {
    width: 25rem;
    height: 10rem;
    background: rgba(46,46,46,255);
    border-radius: 2rem;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    position: absolute;
    top: 13rem;
}
.widgetContent img {
    width: 4rem;
}
.iconInfo {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.iconInfo span {
    color: whitesmoke;
    font-size: 1rem;
    margin-top: 0.3rem;
}


.widgetTitle {
    font-size: 1.5rem;
    font-weight: 600;
    color: white;
}