:root {
    --vh: 100%;
}

a {
    text-decoration: none;
    color: black;
}

/* 기본 사이즈 */
@media all and (min-width: 1025px) {
    .windowHeader {
    width: 100%;
    height: 2.5rem;
    background-color: grey;
    border-radius: 0.5rem 0.5rem 0 0;
    position: relative;
    }
    .windowHeader span {
        font-size: 1.5rem;
        color: white;
        opacity: 0.7;
        justify-items: center;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
    }

    #exitBtn {
        border-radius: 50%;
        background-color: tomato;
        width: 1.2rem;
        height: 1.2rem;
        position: absolute;
        top: 50%;
        left: 1.5rem;
        transform: translate(-50%, -50%);
        pointer-events: all;
        cursor: pointer;
    }

    #outsideContainer {
        border-radius: 0.7rem;
        background-color: darkslategray;
        order: 1;
        height: 80%;
        width: 80%;
        margin: 0 auto;
        display: flex;
        flex-direction: column;
        transition: all 0.7s cubic-bezier(0.1, 0.8, 0.5, 0.9);
        will-change: transform; /* will-change 속성 추가 */
        transform-origin: center left; /* 중심 축을 왼쪽으로 조정 */
        pointer-events: none;
    }

    #outsideContainer.aboutMe,
    #outsideContainer.project,
    #outsideContainer.contactMe {
        width: 17rem;
        height: 17rem;
        margin: 0 0 0 0.5rem;
        font-size: 0.5rem;
        border-radius: 0.7rem;
        background-color: darkslategray;
        display: flex;
        position: absolute;
        top: 15%;
        flex-direction: column;
        transform: perspective(1500px) rotateY(-45deg) scale(0.7);
        will-change: transform;
        transform-origin: center;
        transition: all 0.7s cubic-bezier(0.1, 0.8, 0.5, 0.9);
        pointer-events: all;
    }
    #outsideContainer.project {
        top: 40%;
    }
    #outsideContainer.contactMe {
        top: 65%;
    }

    .insideContainer {
        background-color: whitesmoke;
        border-radius: 0.7rem;
        margin: 0.5rem;
        flex: 1;
        position: relative;
        overflow: hidden;
        height: 100%;
        max-height: 100%;
        overflow: auto;
        pointer-events: all;
    }
    .myImage img {
        width: 90%;
        border-radius: 100%;
        margin: 0 important;
    }

    .projectContainer {
        padding: 3%;
        background-color: darkslategrey;
        height: 100%;
    }

    .projectToggle {
        display: flex;
        flex-direction: column;
        height: 100%;
    }

    .projectBtn {
        width: 100%;
        display: flex;
        justify-content: space-around;
    }
    .projectBtn span {
        width: 100%;
        font-size: 1.5rem;
        text-align: center;
        transition: all 0.25s;
        cursor: pointer;
    }

    .projectPage {
        display: grid;
        grid-template-columns: 2fr 1fr;
        height: 100%;
        grid-template-areas:
            'view info'
            'skills skills';
    }
    .projectPageE {
        display: grid;
        grid-template-columns: 2fr 1fr;
        height: 100%;
        grid-template-areas:
            'view info'
            'skills skills';
    }
    .projectView {
        grid-area: view;
        border-top-left-radius: 1rem;
        overflow: hidden;
        display: flex;
        position: relative;
    }
    .projectView img {
        width: 100%;
        height: 100%;
        object-fit: fill;
    }

    .projectViewE {
        grid-area: view;
        border-top-left-radius: 1rem;
        overflow: hidden;
        display: flex;
        position: relative;
    }
    .projectViewE img {
        width: 100%;
        height: 100%;
        object-fit: fill;
    }

    .nextBtn {
        cursor: pointer;
        position: absolute;
        top: 50%;
        right: 50%;
        transform: translate(50%, -50%);
        display: flex;
        width: 100%;
        padding: 0 3%;
        justify-content: space-between;
    }

    .projectInfo {
        grid-area: info;
        border-top-right-radius: 1rem;
        border-left: 1px dashed black;
        text-align: center;
        background-color: whitesmoke;
        padding: 1rem;
    }
    .infoText {
        margin-bottom: 10%;
    }
    .projectInfo h1 {
        margin: 5% 0 10% 0;
    }
    .projectInfo h3 {
        margin-bottom: 0.5rem;
    }


    .projectSkills {
        grid-area: skills;
        border-bottom-left-radius: 1rem;
        border-bottom-right-radius: 1rem;
        display: flex;
        background-color: whitesmoke;
        border-top: 1px dashed black;
    }

    .aboutProject {
        flex: 1;
        padding: 1%;
        border-right: 1px dashed black;
    }
    .aboutText {
        margin: 3% 4% 1% 4%;
    }
    .projectJob {
        flex: 1;
        padding: 1%;
    }
}

