:root {
    --vh: 100%;
}

/* 기본 사이즈 */
@media all and (min-width: 1025px) {
    #outSideEmail {
        border-radius: 0.7rem;
        background-color: darkslategray;
        order: 1;
        height: 80%;
        width: 50%;
        margin: 0 auto;
        /* margin-left: 10rem; */
        display: flex;
        flex-direction: column;
        transition: all 0.7s cubic-bezier(0.1, 0.8, 0.5, 0.9);
        will-change: transform; /* will-change 속성 추가 */
        transform-origin: center left; /* 중심 축을 왼쪽으로 조정 */
        pointer-events: none;
    }

    .emailContainer {
        text-align: center;
        width: 100%;
        height: 75%;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
    }

    .emailInner {
        width: 60%;
        height: 100%;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        display: flex;
        flex-direction: column;
    }

    .emailText {
        font-size: 2rem;
        font-weight: bold;
        margin-bottom: 10%;
    }

    .emailForm {
        flex: 1;
    }
    .emailForm label {
        font-size: 1.2rem;
    }

    .userName input,
    .userEmail input {
        height: 1.5rem;
        padding-left: 1rem;
    }

    .message textarea {
        padding: 1rem 0 45% 1rem;
    }

    .userName input,
    .userEmail input,
    .message textarea {
        width: 100%;
        pointer-events: all;
        margin: 3% 0 5% 0;
        border-radius: 1rem;
        border: 1px solid grey;
        resize: none;
    }

    .submit input {
        pointer-events: all;
        font-size: 1rem;
        padding: 0.2rem 0.5rem;
    }
}


